@import '../../styles/var.scss';

.cart-form {
  width: calc(50% - 15px);
  //min-height: 70vh;
  box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  padding: 30px;
  margin-left: 15px;

  @include r(992) {
    width: 100%;
    margin-left: 0;
    margin-bottom: 30px;
  }

  @include r (470) {
    padding: 15px;
  }

  &__label {
    display: block;

    &:not(:first-child) {
      margin-top: 15px;
    }
  }

  &__input {
    display: block;
    width: 100%;
    padding: 5px 15px;
    border: 1px solid #353434;
    border-radius: 10px;
    font-size: 16px;
    margin-top: 5px;

  }

  &__error {
    color: #a81414;
    font-size: 12px;
    padding-left: 20px;
    margin-top: 5px;
  }

  &__box {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
  }

  &__btn {
    padding: 10px 20px;
    border-radius: 30px;
    border: 1px solid #353434;
    background-color: transparent;
  }
}